}

#Mostrar {
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction:column;
    text-align: center;
}
#Mostrar h4 { 
  margin-top: 10px;
  margin-bottom:  10px;   
}

#Mostrar p {
    justify-content: left;
    align-items: left;
    text-align: left;
    background-color: rgb(95, 141, 160); 
    width: 300px;
    
 }

.Formulario #muestra {
    position: fixed;
    right: 10;
    left:65.0%;
    z-index:9999;
    opacity: 0.7;
    background: #79a6ee;
    margin-top:3px;
    padding-left: 10px;
    padding-right: 5px;
    border-top-left-radius: 0.5em 1em;
}

